import back from '@/components/back'
import lrz from 'lrz'

export default {
    components:{
        back
    },
    data(){
        return{
            showPopup: false,
            ReportImageType: [],
            PatientId: this.$route.query.PatientId,
            PatientName: this.$route.query.PatientName,
            Diagnosis: '',
            Operation: '',
            DiagnosisArr: [
                '喉血管瘤',
                '喉狭窄',
                '喉鳞状细胞癌',
                '下咽平滑肌肉瘤',
                '下咽血管瘤',
                '下咽恶性肿瘤',
                '下咽鳞状细胞癌',
                '口咽癌',
                '鼻咽癌',
                '鼻窦癌',
                '中耳癌',
                '喉乳头状瘤',
                '颈部脓肿',
                '耳聋',
                '鼾症'
            ],
            OperationArr: [],
            hj: [],
            CT: [],
            DiseaseDesc: ''
        }
    },
    created(){
        this.pull()
    },
    methods:{
        pull(){
            this.$get('/PlatFormAPI/Category/QueryItemByEnum',{
                categoryEnum:25
            }).then(o => {
                this.OperationArr = o
            })
        },
        getImg(type){
            this.nowTarget = type
        },
        update(e){
            const that = this
            const file = e.target.files[0]
            if (file && this.nowTarget){
                lrz(file).then(rst => {
                    var reader = new FileReader();
                    reader.readAsDataURL(file);
                    const that = this
                    reader.onload = function (e) {
                        that.er = this.result.split('base64,')[1]
                        that.$post('PlatFormAPI/PatientArchives/ReportImageUpload',{
                            Base64Str: this.result.split('base64,')[1]
                        }).then(o => {
                            that[that.nowTarget].push(o)
                        })
                    }
                })
                e.target.value = null
            }
        },
        pop(target,idx){
            this[target].splice(idx,1)
        },
        send(){
            if(this.DiseaseDesc.length < 10){
                return this.$toast('请输入至少10个字的病情描述')
            }
            const ImgDetails = []
            this.hj.forEach(v => {
                ImgDetails.push({
                    ImagePaths: v.SavePath,
                    ImageType: 3,
                    PictureLabel: '喉镜'
                })
            })
            this.CT.forEach(v => {
                ImgDetails.push({
                    ImagePaths: v.SavePath,
                    ImageType: 3,
                    PictureLabel: 'CT、MR、DR'
                })
            })
            this.$post('PlatFormAPI/PatientArchives/ReportImagSave',{
                DiseaseDesc: this.DiseaseDesc,
                Diagnosis: this.Diagnosis,
                OperationName: this.Operation.CategoryItemName,
                OperationType: this.Operation.CategoryItemValue,
                MedicalTempId: 2,
                PatientId: this.orderInfo.PatientInfo.PatientId,
                PatientName: this.orderInfo.PatientInfo.PatientName,
                UserType: 2,
                CreateId: this.user.Data.User.DoctorId,
                CreateName: this.user.Data.User.DoctorName,
                ImgDetails
            }).then(o => {
                this.$toast('上传成功')
                this.$router.back()
            })
        }
    }
}



